
/* Se trata de un componente de React que utiliza el componente BasicCard para mostrar tarjetas de estudio
dinmicamente, en base a la leccin seleccionada en el parmetro de la URL "id". Incluye un botn
de actualizacin para volver a la parte superior de la pgina. */

import * as React from 'react';
import CssBaseline from '@mui/material/CssBaseline';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Link from '@mui/material/Link';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import BasicCard from '../BasicCard/BasicCard';
import Footer from '../Footer/Footer';

import { IconButton } from '@mui/material';
import RefreshIcon from '@mui/icons-material/Refresh';
import { useParams } from 'react-router-dom';
import { useState } from 'react';

function Copyright() {

  return (
    <Typography variant="body2" color="text.secondary" align="center">
      {'Copyright  '}
      <Link color="inherit" href="https://mui.com/">
        Your Website
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}
const cards = [1, 2, 3, 4, 5, 6, 7, 8, 9];

const theme = createTheme();


function Palabras() {
//* Est parametro se pasa desde el componente ButtonMenuApp.js
//* Obtenemos el valor del parmetro "id" de la URL que sera leccin 1, 2 o 3 etc
//* Dependiendo del valor de "id" se muestran las palabras de la leccin correspondiente
//* Si el valor es 2 se muestran las palabras de la leccin 2

  const { id } = useParams(); // Obtener el valor del parmetro "id"
  console.log(id);
  let [leccion, setLeccion] = useState(id); // Se inicializa el estado con el valor del parmetro "id"
 
  // Si el parmetro es "Leccin 1" se cambia a "all" para que se muestren todas las palabras
  if (leccion === "Leccin 1") {
    leccion = "1";
  }
  if (leccion === "Leccin 2") {
    leccion = "2";
  }
  if (leccion === "Leccin 3") {
    leccion = "3";
  }
  if (leccion === "Leccin 4") {
    leccion = "4";
  }
  if (leccion === "Leccin 5") {
    leccion = "5";
  }
  if (leccion === "Leccin 6") {
    leccion = "6";
  }
  if (leccion === "Leccin 7") {
    leccion = "7";
  }
  if (leccion === "Leccin 8") {
    leccion = "8";
  }
  if (leccion === "Leccin 9") {
    leccion = "9";
  }
  if (leccion === "Leccin 10") {
    leccion = "10";
  }
  if (leccion === "Leccin 11") {
    leccion = "11";
  }
  if (leccion === "Leccin 12") {
    leccion = "12";
  }
  if (leccion === "Leccin 13") {
    leccion = "13";
  }
  if (leccion === "Leccin 14") {
    leccion = "14";
  }
  if (leccion === "Leccin 15") {
    leccion = "15";
  }
  if (leccion === "Leccin 16") {
    leccion = "16";
  }
  if (leccion === "Leccin 17") {
    leccion = "17";
  }
  if (leccion === "Leccin 18") {
    leccion = "18";
  }
  if (leccion === "Leccin 19") {
    leccion = "19";
  }
  if (leccion === "Leccin 20") {
    leccion = "20";
  }
  if (leccion === "Leccin 21") {
    leccion = "21";
  }
  if (leccion === "Leccin all") {
    leccion = "all";
  }

  
  
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />

      
      <main>
        {/* Hero unit */}
        <Box
          sx={{
            bgcolor: 'background.paper',
            pt: 8,
            pb: 6,
          }}
        >
          <Container maxWidth="sm">
            <Typography
              component="h1"
              variant="h2"
              align="center"
              color="text.primary"
              gutterBottom
            >
              {id}
            </Typography>
  
          </Container>
        </Box>
        <Container sx={{ py: 8 }} maxWidth="md"  >
          {/* End hero unit */}
          
          <Grid container>
            <Grid item xs={12} sm={6} md={4}>
            <BasicCard id ={leccion} sx={{padding: "30px"}}/>
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
            <BasicCard id = {leccion} sx={{padding: "30px"}}/>
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
            <BasicCard id = {leccion} sx={{padding: "30px"}}/>
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
            <BasicCard id = {leccion} sx={{padding: "30px"}}/>
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
            <BasicCard id = {leccion} sx={{padding: "30px"}}/>
            </Grid>
            <BasicCard id = {leccion} sx={{padding: "30px"}}/>
     
          </Grid>
          <Grid container justifyContent="center">
          {/* Recarga la pgina y el scroll se pone al principio */}
          <IconButton sx={{ mt: 2, mb: 2,fontSize: '20rem'}} onClick={() => {
            window.location.reload();
            window.scrollTo(0,0);
          }}>             
              <RefreshIcon />
            </IconButton>
          </Grid>
        </Container>
      </main>
      {/* Footer */}
      <Footer />
    </ThemeProvider>
  );
}

export default Palabras;

